.backdrop-loader {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
}

.loader {
  height: 200px;
  width: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader > div {
  height: inherit;
  width: inherit;
  position: absolute;
}

.loader > div > div {
  height: 25px;
  width: 25px;
  background: #ff6b01;
  border-radius: 50%;
  animation-name: scale;
  animation-duration: 1.1s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  position: absolute;
  top: 0%;
  right: 50%;
  transform: translate(50%)scale(0);
}

.loader > div:nth-child(2) {
  transform: rotate(30deg);
}

.loader > div:nth-child(3) {
  transform: rotate(60deg);
}

.loader > div:nth-child(4) {
  transform: rotate(90deg);
}

.loader > div:nth-child(5) {
  transform: rotate(120deg);
}

.loader > div:nth-child(6) {
  transform: rotate(150deg);
}

.loader > div:nth-child(7) {
  transform: rotate(180deg);
}

.loader > div:nth-child(8) {
  transform: rotate(210deg);
}

.loader > div:nth-child(9) {
  transform: rotate(240deg);
}

.loader > div:nth-child(10) {
  transform: rotate(270deg);
}

.loader > div:nth-child(11) {
  transform: rotate(300deg);
}

.loader > div:nth-child(12) {
  transform: rotate(330deg);
}

.loader > div:nth-child(2) > div {
  animation-delay: .1s;
}

.loader > div:nth-child(3) > div {
  animation-delay: .2s;
}

.loader > div:nth-child(4) > div {
  animation-delay: .3s;
}

.loader > div:nth-child(5) > div {
  animation-delay: .4s;
}

.loader > div:nth-child(6) > div {
  animation-delay: .5s;
}

.loader > div:nth-child(7) > div {
  animation-delay: .6s;
}

.loader > div:nth-child(8) > div {
  animation-delay: .7s;
}

.loader > div:nth-child(9) > div {
  animation-delay: .8s;
}

.loader > div:nth-child(10) > div {
  animation-delay: .9s;
}

.loader > div:nth-child(11) > div {
  animation-delay: 1s;
}

.loader > div:nth-child(12) > div {
  animation-delay: 1.1s;
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
