// header
.header {
  padding: 40px 20px 44px;

  background: rgba(0, 0, 0, 0.56),
    url(/src/images/header/mobile/mobile-bg-1x.png) center/cover no-repeat;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background: rgba(0, 0, 0, 0.56),
      url(/src/images/header/mobile/mobile-bg-2x.jpeg) center/cover no-repeat;
  }

  &__inner-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: flex;
    align-items: flex-end;
  }

  &__logo--title {
    display: none;
  }
}

// nav
.menu {
  &__list {
    display: flex;
  }

  &__item:not(:last-child) {
    margin-right: 40px;
  }

  &__link {
    font-size: 12px;
    line-height: 1.16;
    text-transform: uppercase;

    color: $header-main-color;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &__link:hover,
  &__link:focus {
    color: $accent-color;
  }

  &__item--current::after {
    content: '';
    display: block;

    border: 1px solid $accent-color;
  }
}

.qwe {
  background-color: aqua;
  width: 300px;
  height: 300px;
}

// form
.search-films {
  margin-top: 60px;

  &__inner {
    position: relative;
  }

  &__query {
    width: 100%;
    height: 24px;
    padding: 0 24px 0 4px;

    font-size: 14px;
    line-height: 1.16;

    color: $header-main-color;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $header-main-color;
    outline: none;
  }

  &__query::placeholder {
    color: $header-main-color;
  }

  &__btn {
    position: absolute;
    right: 0;
    top: 0;

    height: 100%;
    width: 24px;

    cursor: pointer;
    border: none;
    background-color: transparent;
  }

  &__btn::before {
    position: absolute;
    top: 5px;
    left: 4px;
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;

    border: 1px solid $header-main-color;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &__btn:hover::before {
    border: 1px solid $accent-color;
  }

  &__btn::after {
    position: absolute;
    top: 14px;
    right: 7px;

    content: '';
    display: block;
    width: 1px;
    height: 6px;
    transform: rotate(-45deg);

    background: $header-main-color;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  &__btn:hover::after {
    background: $accent-color;
  }
}
// error search
.err-sr {
  margin-top: 20px;

  text-align: center;
  font-size: 14px;
  line-height: 1.14;

  opacity: 0;
  color: red;
}

// tablet
@media (min-width: 768px) {
  .header {
    padding: 40px 32px 44px;

    background: rgba(0, 0, 0, 0.56),
      url(/src/images/header/tablet/tablet-bg-1x.png) center/cover no-repeat;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: rgba(0, 0, 0, 0.56),
        url(/src/images/header/tablet/tablet-bg-2x.jpeg) center/cover no-repeat;
    }

    &__logo--title {
      display: block;
      margin-left: 8px;

      font-size: 30px;
      line-height: 1.16;
      color: $header-main-color;
    }
  }

  .search-films {
    margin-top: 40px;

    &__query {
      width: 336px;
    }

    &::after {
      width: 336px;
    }

    &__inner {
      width: 336px;
      margin: 0 auto;
    }
  }
}

// desktop
@media (min-width: 1024px) {
  .header {
    background: rgba(0, 0, 0, 0.56),
      url(/src/images/header/desktop/desktop-bg-1x.png) center/cover no-repeat;

    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: rgba(0, 0, 0, 0.56),
        url(/src/images/header/desktop/desktop-bg-2x.jpeg) center/cover
          no-repeat;
    }

    &__logo--title {
      margin-left: 10px;
    }
  }
}
